import React, { useState } from 'react';
import './App.css';
import MyProfile from './MyProfile';
import Sell from './Sell';
import Market from './Market';
import Auction from './Auction';
import ExportImport from './ExportImport';
import LatestSales from './LatestSales';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser, faDollarSign, faStore, faGavel, faTruck, faListUl } from '@fortawesome/free-solid-svg-icons';
// استيراد مكتبة react-toastify
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App() {
    const [currentView, setCurrentView] = useState('home');
    const [showBalance, setShowBalance] = useState(true);

    return (
        <div className="app-container">
            {/* الصف العلوي الثابت */}
            {showBalance && (
                <div className="top-bar">
                    <p>My Balance: 0 TON</p>
                </div>
            )}

            {/* المحتوى الرئيسي */}
            <div className="main-content">
                {currentView === 'home' && (
                    <div>
                        <h1>Welcome to My App</h1>
                        <p>This is the main content area.</p>
                    </div>
                )}
                {currentView === 'myProfile' && <MyProfile setCurrentView={setCurrentView} />}
                {currentView === 'sell' && <Sell setShowBalance={setShowBalance} />}
                {currentView === 'market' && <Market />}
                {currentView === 'auction' && <Auction />}
                {currentView === 'exportImport' && <ExportImport />}
                {currentView === 'latestSales' && <LatestSales />}
            </div>

            {/* إشعار الرسائل (ToastContainer) */}
            <ToastContainer />

            {/* الصف السفلي الثابت مع الأيقونات */}
            <div className="bottom-bar">
                <div className="section" onClick={() => setCurrentView('myProfile')}>
                    <FontAwesomeIcon icon={faUser} style={{ width: '30px', height: '30px' }} />
                </div>
                <div className="section" onClick={() => setCurrentView('sell')}>
                    <FontAwesomeIcon icon={faDollarSign} style={{ width: '30px', height: '30px' }} />
                </div>
                <div className="section" onClick={() => setCurrentView('market')}>
                    <FontAwesomeIcon icon={faStore} style={{ width: '30px', height: '30px' }} />
                </div>
                <div className="section" onClick={() => setCurrentView('auction')}>
                    <FontAwesomeIcon icon={faGavel} style={{ width: '30px', height: '30px' }} />
                </div>
                <div className="section" onClick={() => setCurrentView('exportImport')}>
                    <FontAwesomeIcon icon={faTruck} style={{ width: '30px', height: '30px' }} />
                </div>
                <div className="section" onClick={() => setCurrentView('latestSales')}>
                    <FontAwesomeIcon icon={faListUl} style={{ width: '30px', height: '30px' }} />
                </div>
            </div>
        </div>
    );
}

export default App;
