import React, { useState } from 'react';

const Sell = ({ setShowBalance }) => {
    const [currentTab, setCurrentTab] = useState('myItems'); // تتبع القسم الحالي

    React.useEffect(() => {
        setShowBalance(false);
        return () => setShowBalance(true);
    }, [setShowBalance]);

    return (
        <div>
            {/* شريط ثابت في الأعلى */}
            <div style={{ display: 'flex', justifyContent: 'space-around', marginBottom: '20px' }}>
                <button onClick={() => setCurrentTab('myItems')}>My Items</button>
                <button onClick={() => setCurrentTab('forSale')}>For Sale</button>
                <button onClick={() => setCurrentTab('forAuction')}>For Auction</button>
            </div>

            {/* المحتوى الرئيسي */}
            <div>
                {currentTab === 'myItems' && (
                    <div>
                        <h2>My Items</h2>
                        {[...Array(6)].map((_, i) => (
                            <div key={i}>My Item {i + 1}</div>
                        ))}
                    </div>
                )}

                {currentTab === 'forSale' && (
                    <div>
                        <h2>For Sale</h2>
                        {[...Array(6)].map((_, i) => (
                            <div key={i}>Item for Sale {i + 1}</div>
                        ))}
                    </div>
                )}

                {currentTab === 'forAuction' && (
                    <div>
                        <h2>For Auction</h2>
                        {[...Array(6)].map((_, i) => (
                            <div key={i}>Item for Auction {i + 1}</div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default Sell;
