import React, { useEffect, useState } from 'react';
import { TonConnectButton } from '@tonconnect/ui-react';
import { toast } from 'react-toastify';

// مكون زر مستقل
const Button = ({ children, onClick, backgroundColor = '#0d6efd', width = '40%', ...props }) => (
    <button
        style={{
            backgroundColor,
            color: 'white',
            padding: '10px 20px',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            margin: '5px',
            width,
            ...props.style,
        }}
        onClick={onClick}
    >
        {children}
    </button>
);

const MyProfile = ({ setCurrentView }) => {
    const [profilePicture, setProfilePicture] = useState('');
    const [showImportModal, setShowImportModal] = useState(false);
    const [collectibles, setCollectibles] = useState([]);

    // جلب بيانات المستخدم من Telegram WebApp
    useEffect(() => {
        if (window.Telegram && window.Telegram.WebApp) {
            const user = window.Telegram.WebApp.initDataUnsafe?.user;
            if (user && user.photo_url) {
                setProfilePicture(user.photo_url);
            }
        }
    }, []);

    // وظيفة لتحميل المقتنيات (مثال)
    useEffect(() => {
        const fetchCollectibles = async () => {
            try {
                const response = await fetch('https://api.example.com/collectibles');
                const data = await response.json();
                setCollectibles(data);
            } catch (error) {
                console.error('Error fetching collectibles:', error);
            }
        };

        fetchCollectibles();
    }, []);

    // وظائف التنقل
    const goToMarket = () => {
        setCurrentView('market'); // تحديث العرض إلى تبويب السوق
    };

    const openImportModal = () => {
        setShowImportModal(true);
    };

    const closeImportModal = () => {
        setShowImportModal(false);
    };

    // نسخ اسم المستخدم إلى الحافظة مع رسالة تأكيد
    const copyUsername = () => {
        navigator.clipboard.writeText('@S2ES2');
        toast.success('Username copied to clipboard!');
    };

    // فتح ملف المستخدم الشخصي في Telegram
    const openTelegramProfile = () => {
        const user = window.Telegram?.WebApp?.initDataUnsafe?.user;
        if (user && user.username) {
            window.open(`https://t.me/${user.username}`, '_blank');
        } else {
            toast.error('Unable to fetch your Telegram profile.');
        }
    };

    return (
        <div style={{ textAlign: 'center' }}>
            {/* زر ربط المحفظة */}
            <div style={{ marginBottom: '20px' }}>
                <TonConnectButton />
            </div>

            {/* صورة الملف الشخصي */}
            <div
                style={{
                    width: '100px',
                    height: '100px',
                    borderRadius: '50%',
                    backgroundColor: '#ccc',
                    margin: '20px auto',
                    backgroundImage: profilePicture ? `url(${profilePicture})` : 'none',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                }}
            ></div>

            {/* قسم المقتنيات */}
            <div
                style={{
                    height: '70vh',
                    border: '1px solid #ddd',
                    borderRadius: '10px',
                    padding: '20px',
                    position: 'relative',
                }}
            >
                <h3 style={{ marginBottom: '10px' }}>My Collectibles</h3>

                {/* عرض المقتنيات */}
                {collectibles.length > 0 ? (
                    <div
                        style={{
                            display: 'grid',
                            gridTemplateColumns: 'repeat(3, 1fr)',
                            gap: '10px',
                        }}
                    >
                        {collectibles.map((item, index) => (
                            <div
                                key={index}
                                style={{
                                    width: '100%',
                                    height: '100px',
                                    backgroundColor: '#f0f0f0',
                                    borderRadius: '10px',
                                    backgroundImage: `url(${item.image})`,
                                    backgroundSize: 'cover',
                                    backgroundPosition: 'center',
                                }}
                            ></div>
                        ))}
                    </div>
                ) : (
                    <>
                        {/* المربعات الفارغة التي تمثل المقتنيات */}
                        <div
                            style={{
                                display: 'grid',
                                gridTemplateColumns: 'repeat(3, 1fr)',
                                gap: '10px',
                                position: 'relative',
                                zIndex: 0,
                                marginTop: '20px',
                            }}
                        >
                            {[...Array(6)].map((_, i) => (
                                <div
                                    key={i}
                                    style={{
                                        width: '100%',
                                        height: '100px',
                                        backgroundColor: '#f0f0f0',
                                        borderRadius: '10px',
                                    }}
                                ></div>
                            ))}
                        </div>

                        {/* الطبقة السوداء الشفافة فوق المربعات فقط */}
                        <div
                            style={{
                                position: 'absolute',
                                top: '80px', // بدء الطبقة من تحت كلمة "My Collectibles"
                                left: '20px', // نفس padding القسم
                                right: '20px', // نفس padding القسم
                                bottom: '80px', // ترك مسافة أكبر للشريط السفلي
                                backgroundColor: 'rgba(0, 0, 0, 0.5)', // شفافية 50%
                                zIndex: 1,
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center',
                                alignItems: 'center',
                                color: 'white',
                                borderRadius: '10px', // نفس border-radius القسم
                            }}
                        >
                            <p>You have no collectibles. To add collectibles, choose the following:</p>
                            {/* الأزرار الزرقاء بعرض 40% وتوسيطها */}
                            <Button onClick={goToMarket}>Buy Gift</Button>
                            <Button onClick={openImportModal} style={{ whiteSpace: 'nowrap' }}>Import Gift</Button>
                        </div>
                    </>
                )}
            </div>

            {/* النافذة المنبثقة لاستيراد الهدية */}
            {showImportModal && (
                <div
                    style={{
                        position: 'fixed',
                        top: '0',
                        left: '0',
                        right: '0',
                        bottom: '0',
                        backgroundColor: 'rgba(50, 50, 50, 0.9)', // لون رمادي مائل للسواد
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        zIndex: 9999,
                    }}
                >
                    <div
                        style={{
                            backgroundColor: 'white',
                            padding: '20px',
                            borderRadius: '10px',
                            textAlign: 'center',
                        }}
                    >
                        <p>To import a gift from your Telegram profile, transfer the gift to the following account:</p>
                        <p><strong>@S2ES2</strong></p>
                        {/* أزرار النافذة المنبثقة */}
                        <Button onClick={copyUsername} backgroundColor="#28a745" width="45%">
                            Copy
                        </Button>
                        <Button
                            onClick={openTelegramProfile} // توجيه المستخدم إلى ملفه الشخصي في Telegram
                            backgroundColor="#0d6efd"
                            width="45%"
                        >
                            Go
                        </Button>
                        <Button onClick={closeImportModal} backgroundColor="#dc3545" style={{ marginTop: '10px' }} width="45%">
                            Close
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default MyProfile;
